import React from "react";
import Link from "gatsby-link";
import URL from "../components/URL";
import styles from './cjeline.module.scss';



class Jedinica extends React.Component {
componentDidMount() {
  let eq = document.querySelectorAll('math');
  console.log(eq);
  eq.forEach((jed)=>{
    var inline = !jed.attributes.inline;
    
    katex.render(jed.innerHTML, jed, {displayMode: inline});
  })

  }
  render() {

      const { markdownRemark: post } = this.props.data;
const src = `https://www.youtube.com/embed/${post.frontmatter.video}?rel=0&amp;showinfo=0`;

    return (
      <div>
   
    <div className="container content">
    
         <h2> {post.frontmatter.title}</h2>
         <div className="breadcrumbs"><span><Link to="/">Naslovna</Link> </span><span>/ <Link to={`/${post.frontmatter.razred}/`}>Matematika {post.frontmatter.razred}</Link> </span>/ <URL to={`/${post.frontmatter.razred}/${post.frontmatter.cjelina}/`}>{post.frontmatter.cjelina}</URL></div>
      <div> <small>Type: {post.frontmatter.type} /  VideoID: {post.frontmatter.video} </small> </div>
                <div>
                <div dangerouslySetInnerHTML={{ __html: post.html }} />
                </div>
    </div>
    </div>
    );
  }
}
export default Jedinica;


 
/* eslint no-undef: "off"*/
export const pageQuery = graphql`
   query JedinicaQuery( $razred: Int, $cjelina: String ,$lekcija: String,$title: String) {
    markdownRemark(
      frontmatter: { 
         razred: {eq: $razred}
        cjelina: {eq: $cjelina}
        lekcija: {eq: $lekcija}
        title: {eq: $title}
      }
    ) {
      html
      frontmatter {
        razred
        cjelina
        lekcija
        title
        video
        type
      }
    }
  }
`;



