import React, { Component } from "react";
import Link from 'gatsby-link';


class URL extends Component {

	render(){	
		// console.log(this.props);
		var url = this.props.to.toLowerCase().replace(/ž/g, "z").replace(/š/g, "s").replace(".", "-").replace(/č/g, "c").replace(/ć/g, "c").replace(/đ/g, "d").replace(/(\.\s+|\s+|\.)/g, "-");
		return <Link className={this.props.className} to={url}>
					{this.props.children}
  					
				</Link>;
	}		
}

export default URL


